import React from "react";
export const TodoList = ({ todos, toggleTodo }) => {
  return (
    <div>
      {todos.map((todo) => (
        <button
          style={{
            fontSize: 30,
            boxShadow: "1px 2px 4px grey",
            padding: 5,
            width: "100%",
            margin: 5,
            textAlign: "left",
            paddingLeft: 10,
            background: "white",
            border: "none",
            textDecorationLine: todo.completed ? "line-through" : null,
          }}
          key={todo.id}
          onClick={() => toggleTodo(todo.id)}
        >
          {todo.name}
        </button>
      ))}
    </div>
  );
};
